import React, {useState, useEffect} from 'react';
import cn from 'classnames';
import {Link} from 'react-router-dom';
import './Header.scss';
import { GiHamburgerMenu } from "react-icons/gi";
import { FaUserCircle } from "react-icons/fa";


const Header = () => {
    // click하면 메뉴 열리고 닫히기
    const [opener, setOpener] = useState(false);
    const openclose = () => {
        setOpener(!opener)
    }

    // hover하면 메뉴 열리기
    const [on, setOn] = useState(false);


    // scroll이벤트
    const useScroll = () => {
        const [scy, setScy] = useState({y : 0})
        const onScroll = () => {
            setScy({
                y : window.scrollY
            })
        }
        useEffect(
            () => {
                window.addEventListener("scroll", onScroll)
                return () => window.removeEventListener("scroll", onScroll)
            }, [] // [] = useEffect 한번만 수행
        )
        return scy;
    }

    const { y } =useScroll();
    console.log(y);

    return (
        <div className="headerWrap" style={{backgroundColor: y ? "#fff" : "transparent", color: y ? "#ff385c" : "#666"}}>
            <div className="row">
                <h1 className="logo">
                    <Link to="/" className="lnk"><img src="images/logo.png" alt=""/></Link>
                </h1>
                <nav className="nav">
                    <ul className="depth1">
                        <li onMouseEnter={() => setOn(true)} onMouseLeave={() => setOn(false)}>
                            국제어린이마라톤이란
                            <ul className={cn("depth2", {on})}>
                                <li><Link to='/product' className="lnk">마라톤의 의미</Link></li>
                                <li>우리가 만드는 변화</li>
                            </ul>
                        </li>
                        <li>
                            <Link to="/news">마라톤행사안내</Link>
                        </li>
                        <li>
                            참가신청
                        </li>
                        <li>
                            <Link to="/menutab">커뮤니티</Link>
                        </li>
                    </ul>
                </nav>
                <div className="mobileNav">
                    <button onClick={openclose}>
                        <GiHamburgerMenu className="ham"/><FaUserCircle className="person"/>
                    </button>
                    <div className={cn("mobileNavList", {opener})}>
                        <ul>
                            <li><Link to="/login">Login</Link></li>
                            <li><Link to="/join">Join</Link></li>
                            <li><Link to="/product">Product</Link></li>
                            <li><Link to="/RoadMap">RoadMap</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Header;