import React from 'react';
import './First.scss';

const First = () => {
    return (
        <div className="sub1Wrap">
            <div className="sub1H1">
                <h1>세이브더칠드런 100년의 믿음,<br/><strong>각자의 발걸음</strong>으로 전 세계 아동을 구할 수 있습니다.</h1>
            </div>
            <div className="sub1Text">
                <div className="sub1Img"><img src="images/mean-img01.png" alt=""/></div>
                <ul>
                    <li>종교, 국적, 인종을 초월해 모든 아동이 온전히 권리를 누리는 세상을 위해
                        100년간 달려온 세이브더칠드런은 2011년 부터 4km 미니코스로 구성된
                        국제어린이마라톤을 진행하고 있으며, 올해 10주년을 맞이하였습니다.</li><br/>
                        <li>2020년 국제어린이마라톤에서는 코로나19로 인해
                        모두가 같은 날, 같은 장소에 모일 순 없지만 원하는 시간과 장소에서
                        각자의 발걸음으로 전 세계 아동들을 구할 수 있습니다.</li><br/>
                        <li>세이브더칠드런의 마라톤 어플리케이션을 활용하여,
                        1km마다 전달되는 휴대폰 알림(push)과
                        기념품으로 제공되는 포토카드를 통해
                        아동권리 실현을 위한 체험 미션을 수행할 수 있습니다.</li><br/>
                        <li>우리가 아이를 구하면 아이가 세상을 구한다는 믿음,
                        2020 세이브더칠드런 국제어린이마라톤에서도 함께 해주세요.</li>
                </ul>
            </div>
        </div>
    );
};

export default First;