import React from 'react';
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import './Homesection.scss';
import { FiChevronLeft, FiChevronRight } from 'react-icons/fi';
import styled from 'styled-components';

const Article = styled.div`
    position: relative;
    z-index: 1;
    margin-top: 80px;
    .slick-arrow {
        position:absolute;
        top: 50%;
        transform: translateY(-50%);
        background: none;
        padding: 10px;
        z-index: 9999;
        width: 80px; // 화살표 사이즈
        height: 80px; // 화살표 사이즈
        color: #000;
    }
    .slick-prev {
        left: 100px;
    }
    .slick-next {
        right: 100px;
    }
    .slick-dots {
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translate(-50%);
        li {
            display: inline-block;
            &+li { padding-left: 10px;} //li의 바로 다음 li 지목 (& 본인 +다음 (li) 바로다음요소) (css에서는 li+li)
            button {
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background: #fff;
                text-indent: -99999px;
                overflow: hidden;
            }
        }
        .slick-active {
            button{
                background: #999;
            }
        }
    }
    .slide {
        position: relative;
        height: 70vh !important;
        // 화면넓이기준단위 vw, 50vw = 화면넓이의 50%, 우선순위높이기 !important
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        .text {
            position: absolute;
            text-align: center;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            color: #fff;
            font-size: 50px;
        }
        button {
            padding: 10px;
            border-radius: 10px;
            backgorund: #fff;
            &:hover {
                background: red;
                color: lime;
            }
        }
    }
    .slide1 {
        background-image: url('images/main_swipe_1.jpg');
    }
    .slide2 {
        background-image: url('images/main_swipe_2.jpg');
    }
    .slide3 {
        background-image: url('images/main_swipe_3.jpg');
    }
`;


const Homesection = () => {

    const settings = { // slick slide
        autoplay: true,
        autoplaySpeed: 4000,
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        prevArrow:<FiChevronLeft/>,
        nextArrow:<FiChevronRight/>
    };

    return (
        <div className="homesecWrap">
            <Article className="article1">

                <Slider {...settings}> {/* slick slide*/}
                    <div className="slide slide1">
                        <div className="text row">
                        
                        </div>
                    </div>
                    <div className="slide slide2">
                        <div className="text row">
                        
                        </div>
                    </div>
                    <div className="slide slide3">
                        <div className="text row">
                        
                        </div>
                    </div>
                </Slider>
                
            </Article>
            <article className="vidBg">
                <div className="mainVid">
                    <iframe width="727px" height="409px" src="https://www.youtube.com/embed/3nnfdErsadk" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </div>
            </article>
            
            <article className="article2">
                <div className="row">
                    <ul>
                        <li>
                            <a href="#">
                                <div>
                                    전체사진
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <div>
                                    중부권
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <div>
                                    남부권
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <div>
                                    동부권
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <div>
                                    호남권
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <div>
                                    충청권
                                </div>
                            </a>
                        </li>
                    </ul>
                    <div className="lifeStyle">
                        <ul>
                            <li>
                            <img src="images/ch1.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch2.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch3.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch4.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch5.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch6.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch7.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch8.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch9.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch10.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch11.jpg" alt=""/>
                            </li>
                            <li>
                            <img src="images/ch12.jpg" alt=""/>
                            </li>
                        </ul>
                    </div>
                </div>
            </article>
        </div>
    );
};

export default Homesection;