import React from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';

const RoadMapBlock = styled.div`
    border-top: 80px solid orange;
    padding-top: 50px;
`;

const RoadMap = () => {
    return (
        <RoadMapBlock>
            <Header/>
            <div className="row" dangerouslySetInnerHTML={{__html:`<iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3165.317277302148!2d127.02562700142843!3d37.50043438556859!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xfff592df39ec933a!2z6re466aw7Lu07ZOo7YSw7JWE7Yq47ZWZ7JuQIDXrs4TqtIA!5e0!3m2!1sko!2skr!4v1606967050759!5m2!1sko!2skr" width="100%" height="800px" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>`}}></div>
            <Footer/>
        </RoadMapBlock>
    );
};

export default RoadMap;